.grid-item {
  font-family: Helvetica;
  width: 280px;
  padding: 10px;
  min-height: 40px;
  img {
    max-width: 100%;
  }
  h4 {
    margin: 5px 0;
    font-size: 16px;
    color: #222;
  }
  p {
    margin: 5px 0;
    font-size: 13px;
    color: #666;
  }
}

.resp-media {
  position: relative;
  padding-bottom: 67.5%;
  height: 0;
  margin: 0;
  overflow: hidden;
  iframe,
  object,
  embed,
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}